.login-wrap {
    width: 640px;
    height: 400px;
    display: flex;
    .left {
        width: 50%;
        .login-bg {
            width: 100%;
            height: 100%;
        }
    }
    .right {
        flex-grow: 1;
        .icon {
            width: 64px;
            height: 64px;
            display: block;
            margin: 50px auto 70px;
        }
        .btn-block {
            width: 100%;
            display: flex;
            flex-direction: column;
            height: 200px;
            justify-content: space-around;
            padding: 0 50px;
            .ant-input-affix-wrapper {
                border-radius: 50px;
            }
        }
    }
}